/* eslint-disable no-nested-ternary */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable radix */
/* eslint-disable global-require */
/* eslint-disable max-len */
/* eslint-disable import/no-unresolved */
/* eslint-disable react/require-default-props */
/* eslint-disable react/forbid-prop-types */
/* eslint-disable no-unused-vars */
import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Helmet from "react-helmet";
import Loader from "Components/Loader";
import Message from "Components/Message";
import noImage from "../../Components/assets/noPosterSmall.png"; // this is faster than using  null ? a : b

const Container = styled.div`
  height: calc(100vh - 50px);
  width: 100%;
  position: relative;
  padding: 50px;
`;
// blur effect on background image
const Backdrop = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(${(props) => props.bgImage});
  background-position: center center;
  background-size: cover;
  filter: blur(3px);
  opacity: 0.5;
  z-index: 0;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
  position: relative;
  z-index: 1;
  height: 100%;
`;

const Cover = styled.div`
  width: 30%;
  background-image: url(${(props) => props.bgImage});
  background-position: center center;
  background-size: cover;
  height: 100%;
  border-radius: 5px;
`;

const Data = styled.div`
  width: 70%;
  margin-left: 10px;
`;

const Title = styled.h3`
  font-size: 32px;
`;
const ItemContainer = styled.div`
  margin: 10px 0px;
`;
const Item = styled.span``;
const Divider = styled.span`
  margin: 0px 10px;
`;
const Overview = styled.p`
  font-size: 12px;
  opacity: 0.7;
  line-height: 1.5;
  width: 50%;
`;

const DetailPresenter = ({ result, loading, error }) =>
  loading ? (
    <>
      <Helmet>
        <title>Loading | Netflix</title>
      </Helmet>
      <Loader />
    </>
  ) : error ? (
    <Message text={error} color="#e74c3c" />
  ) : (
    <Container>
      <Helmet>
        <title>{result.original_title || result.original_name} | Netflix</title>
      </Helmet>
      <Backdrop bgImage={`https://image.tmdb.org/t/p/original${result.backdrop_path || result.poster_path}`} />
      <Content>
        <Cover bgImage={result.poster_path ? `https://image.tmdb.org/t/p/original${result.poster_path}` : noImage} />
        <Data>
          <Title>{result.original_title || result.original_name}</Title>
          <ItemContainer>
            <Item>{result.release_date ? result.release_date.substring(0, 4) : result.first_air_date.substring(0, 4)}</Item>
            <Divider>‚ñ™</Divider>
            <Item>{result.runtime === 0 || result.runtime ? result.runtime : result.episode_run_time[0]} min</Item>
            <Divider>‚ñ™</Divider>
            <Item>{result.genres && result.genres.map((genre, index) => (index === result.genres.length - 1 ? genre.name : `${genre.name} / `))}</Item>
            <Divider>‚ñ™</Divider>
            <Item>{result.vote_average > 0 ? `${result.vote_average} ‚≠ê` : ``}</Item>
            <Item>{parseInt(result.vote_average, 10) > 2 ? `‚≠ê` : ``}</Item>
            <Item>{parseInt(result.vote_average, 10) > 4 ? `‚≠ê` : ``}</Item>
            <Item>{parseInt(result.vote_average, 10) > 6 ? `‚≠ê` : ``}</Item>
            <Item>{parseInt(result.vote_average, 10) > 8 ? `‚≠ê` : ``}</Item>
          </ItemContainer>
          <Overview>{result.overview}</Overview>
        </Data>
      </Content>
    </Container>
  );

// Constraint Check
DetailPresenter.propTypes = {
  result: PropTypes.object,
  loading: PropTypes.bool.isRequired,
  error: PropTypes.string,
};

export default DetailPresenter;
